import './App.css';
import Home from "../home/Home";
import Footer from "../footer/Footer";
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import NavbarComponent from "../navbar/NavbarComponent";
import {
    HOME_URL, ITEM_URL,
    ROOT_URL, SEARCH_URL,
    SELLER_CREATE_NEW_ITEM_URL,
    SELLER_ITEMS_URL,
    SELLER_URL
} from "../../constants/application/ApplicationUrl";
import SellerHome from "../seller/SellerHome";
import SellerItems from "../seller/SellerItems";
import SellerCreateItem from "../seller/SellerCreateItem";
import Item from "../item/Item";
import SearchPage from "../search/SearchPage";

function App() {
    return (
        <div className="App d-flex flex-column">
            <NavbarComponent/>
            <Router>
                <div className={"flex-fill"}>
                    <Switch className={""}>
                        <Redirect from={ROOT_URL} to={HOME_URL} exact={true}/>
                        <Route path={SEARCH_URL} exact={true} component={SearchPage}/>
                        <Route path={HOME_URL} exact={true} component={Home}/>
                        <Route path={ITEM_URL} exact={true} component={Item}/>
                        <Route path={SELLER_URL} exact={true} component={SellerHome}/>
                        <Route path={SELLER_ITEMS_URL} exact={true} component={SellerItems}/>
                        <Route path={SELLER_CREATE_NEW_ITEM_URL} exact={true} component={SellerCreateItem}/>
                    </Switch>
                </div>
            </Router>
            <Footer/>
        </div>
    );
}

export default App;
